
import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';


export default class Login extends Component{
	constructor(props){
		super(props);
		this.state={
			email:'',
			password:'',
			showForgetPassword:false
		}
	}

	
	handleLoginInput(e){
		var name=e.target.name,value=e.target.value;
		this.setState({[name]:value});
	}

	login(e){
		e.preventDefault();
		var logindata1=this.state;
		console.log(logindata1)
		
		fetch('http://192.168.20.168:4000/api/login',{
			mode:'cors',
			method:"POST",
			headers: { 'Accept':'application/json , text/plain, */*',
						'Content-type':'application/json' },
			body:JSON.stringify(logindata1)
		}).then((res) => res.json())
		.then(res => {
			if(typeof res === "object"){
				console.log("response", res);
			}
		})
		.catch((err) => console.log(err))
	}

	forgetPassword(){
		this.setState({showForgetPassword:!this.state.showForgetPassword})
		console.log("forget clicked")
	}

	handleForgetPassword(e){
		var name=e.target.name;
		var value=e.target.value;
		this.setState({[name]:value})
		
	}

	fp_submit(e){
		e.preventDefault();
		var email = this.state;
		console.log("form", email)
		fetch('http://192.168.20.168:4000/api/forgot_password',{
			mode:'cors',
			method:"POST",
			headers: { 'Accept':'application/json , text/plain, */*',
						'Content-type':'application/json' },
			body:JSON.stringify(email)
		}).then((response) => response.json())
			.then(response => {
				if(typeof response === "object"){
					console.log("response", response);
				}
			})
		.catch((err) => console.log(err))
	}
	render(){
		const {email,password}= this.state;
		const isEnabled= email.length > 0 && password.length == 4;
		
		
		return(
				
					<div className="login_section">
						<div className="login_img">
							<span className="login_text">Log In</span>
							<img src="../assets/images/login/login.png" alt="login_img"/>
						</div>
						<div className="login_input">
							<form onSubmit={this.login.bind(this)}>
								<div className="form-group">
									<input 
										type="email" 
										name="email" 
										className="user_inputs_login form-control" 
										placeholder="Email" 
										value={this.state.email} 
										onChange={this.handleLoginInput.bind(this)}/>
								</div>
								<div className="form-group">
									<input 
										type="password" 
										name="password" 
										className="user_inputs_login form-control" 
										placeholder="Password" 
										value={this.state.password} 
										onChange={this.handleLoginInput.bind(this)}/>
								</div>
								<div className="form-group">
										<span><a onClick={this.forgetPassword.bind(this)}>Forgot password?</a></span>
								</div>
								<div className="form-group text-right">
									<input 
										type="submit" 
										name="Login_submit" 
										value="Log In" 
										className={isEnabled==true?"submit_button":"IMDisabled"}
										disabled={!isEnabled}/>
								</div>
							</form>
						</div>
						<div className={this.state.showForgetPassword==false? "hide-modal":"modal"}>
							<div className="modal-content">
								<form onSubmit={this.fp_submit.bind(this)}>
									<div className="form-group">
										<input 
											type="email" 
											className="form-control" 
											name="email" 
											placeholder="Enter Registered Your Email" 
											value={this.state.email} 
											onChange={this.handleForgetPassword.bind(this)}/>
									</div>
									<div className="form-group text-right">
										<input 
											type="submit" 
											name="fp_submit" 
											value="Send" 
											className="submit_button"/>
										<button className="submit_button close_btn" onClick={this.forgetPassword.bind(this)}>Close</button>
									</div>
								</form>
							</div>
						</div>
					</div>			
						
				
			
				
		)
	}
}


